.bottom-bar {
    position: absolute;
    bottom: 0;
    text-align: center;
    width: 100%;
    border-top: 1px solid #ddd;
}

.bottom-bar .range-control-range { background-color: #ccc; margin-right: 10px; padding-left: 2px; padding-right: 2px}
.bottom-bar .range-marker { color: #fff}
.pipe-wrapper { display: -ms-inline-flexbox; display: inline-flex; width: 100% }
.pipe-wrapper .pipe-info { width: 100% }
.pipe-row{ display: -ms-flexbox; display: flex; -ms-flex-pack: justify; justify-content: space-between; margin-left: 10px;}
.pipe-row.inline {
    -ms-flex-pack: normal;
        justify-content: normal;
}
.dep-control:hover {
    background-color: #dddddd;
}

.create-action {
    visibility: hidden;
}
.dep-control:hover .create-action {
    visibility: visible;
}

.hints {
    margin-top: calc(100vh - 300px);
}



